@import "styles";

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loading-spinner {
  width: 50px;
  height: 50px;
  border: 10px solid $secondary-color;
  border-top: 10px solid $primary-color;
  border-radius: 50%;
  animation: spinner 1.5s linear infinite;
  justify-content: center;
  margin: 10rem 50%;
}
